
.btn{
	border: 2px solid transparent;
	padding: 9px 12px;
	-webkit-box-shadow: 1px 1px 2px rgba(0,0,0,0.4);
	        box-shadow: 1px 1px 2px rgba(0,0,0,0.4);
	border-radius: 3px;

	margin: 200px 10px;
	position: relative;
	overflow: hidden;
}

.btn:focus{
	outline: none;
	border: 2px solid brown;
}

.redDot{
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background: pink;
	opacity: 0.3;

	position: absolute;
	-webkit-animation: 0.5s big forwards;
	        animation: 0.5s big forwards;
}


@-webkit-keyframes big{
	0{ -webkit-transform: scale(0.1); transform: scale(0.1)}
	100%{ -webkit-transform: scale(20); transform: scale(20)}
}


@keyframes big{
	0{ -webkit-transform: scale(0.1); transform: scale(0.1)}
	100%{ -webkit-transform: scale(20); transform: scale(20)}
}



